* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  background-color: #eff3f7;
  height: 100%;
}

body,
#root {
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.container {
  height: 100%;
  width: 80%;
  display: flex;
  justify-content: center;
}
.container .loader {
  color: rgba(0, 0, 0, 0.384);
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: 300;
}

.movies {
  padding: 70px 70px;

  display: grid;
  grid-template-columns: repeat(2, minmax(500px, 1fr));
  column-gap: 65px;
  row-gap: 90px;
  font-weight: 300;
}

.movies .movie {
  border-radius: 5px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  background-color: white;
  padding: 20px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}
.movies .movie img {
  max-width: 150px;
  width: 100%;
  position: relative;
  top: -50px;
  margin-right: 30px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie .movie__title,
.movie .movie__year,
.movie .movie__rating {
  font-weight: 300;
}
.movie .movie__title {
  font-size: 24px;
  margin-bottom: 5px;
}
.movie .movie__year {
  margin-bottom: 5px;
}
.movie .movie__genres {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  opacity: 0.5;
  margin-bottom: 10px;
}
.movie .movie__genres li {
  margin-right: 10px;
  font-size: 14px;
}

.movie .movie__summary {
  opacity: 0.5;
  margin-bottom: 10px;
}

.movie .movie__rating {
  font-size: 14px;
  opacity: 0.5;
}

@media screen and (max-width: 1130px) {
  .movies {
    grid-template-columns: repeat(1, 1fr);
  }
}
@media screen and (max-width: 700px) {
  .container {
    width: 100%;
    font-size: 10px;
  }
  .movies {
    padding: 70px 20px;
  }
  .movies .movie {
    padding: 12px;
    font-size: 10px;
  }
  .movies .movie img {
    max-width: 80px;
    width: 100%;
    top: -30px;
    margin-right: 20px;
  }
  .movie .movie__title {
    font-weight: 300;
    font-size: 15px;
  }
  .movie .movie__year {
    font-size: 9px;
  }
  .movie .movie__genres li {
    font-size: 8px;
    margin-right: 6px;
  }
  .movie .movie__rating {
    font-size: 10px;
  }
}
